.search-box{
    width: 20em;
    aspect-ratio: 8;
    padding: 0.75em;
    box-sizing: border-box;
    outline: 0;
    border-radius: 0;
    border: 0.025em solid blueviolet;
    color: blueviolet;
    font-family: 'Gill Sans'; font-weight: 500; font-style: normal !important;
    font-size: 1.1em;
    transition: transform 0.25s ease-out;
  }
  
.search-box::placeholder{
  color: blueviolet;
}

.search-box:hover{
  transform: scale(1.05);
}
  
  
