@font-face {
  font-family: 'Bigelow Rules';
  src: url('./fonts/BigelowRules/BigelowRules-Regular.eot');
  src: url('./fonts/BigelowRules/BigelowRules-Regular.eot?#iefix') format('embedded-opentype'),
    url('./fonts/BigelowRules/BigelowRules-Regular.woff2') format('woff2'),
    url('./fonts/BigelowRules/BigelowRules-Regular.woff') format('woff'),
    url('./fonts/BigelowRules/BigelowRules-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Gill Sans';
  src: url('./fonts/GillSans/GillSans-Medium.eot');
  src: url('./fonts/GillSans/GillSans-Medium.eot?#iefix') format('embedded-opentype'),
       url('./fonts/GillSans/GillSans-Medium.woff2') format('woff2'),
       url('./fonts/GillSans/GillSans-Medium.woff') format('woff'),
       url('./fonts/GillSans/GillSans-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@media (max-width: 450px) {
  .app {
    font-size: 14px;
  }
}

.app {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(135deg, #6665DD, #BA3C79);
}

.app *{
  -webkit-transition: all 0.25s ease-in-out !important;
  -moz-transition: all 0.25s ease-in-out !important;
  -ms-transition: all 0.25s ease-in-out !important;
  -o-transition: all 0.25s ease-in-out !important;
  transition: all 0.25s ease-in-out !important;
}

.hide {
  opacity: 0;
}
